import { useLocation } from "react-router-dom";
import UserMemberContainerWrapper from "../../../Ui/UserMember/UserMemberContainerWrapper";
import UserMemberContentWrapper from "../../../Ui/UserMember/UserMemberContentWrapper";
import BorderBottom from "../../../Ui/Border/BorderBottom";
import style from "../../FinanceManager/FinanceManager.module.css";
import { useTranslation } from "react-i18next";
import stylePayroll from "../PayrollManager.module.css";
import { useEffect, useState } from "react";
import {
  usePayrollStaffTdsCompute,
  usePayrollStaffTdsProfileUpdate,
} from "../../../Hooks/ApiHooks/PayrollManager/payroll-manager-api";
import QvipleLoader from "../../../Loader/QvipleLoader";
import TileWrapper from "../../../Ui/Tab/Tile/TileWrapper";
import TileCard from "../../../Ui/Tab/Tile/TileCard";
import CreateButton from "../../../Ui/Button/CreateButton";
import FormInput from "../../../Ui/Input/FormInput";
import { useOneStaffProfile } from "../../../../hooks/institute/institute-staff-student-api";
import payrollStyle from "../PayrollManager.module.css";

const PayrollTdsCalculationUpdateForm = () => {
  const getQuery = useLocation();
  const { t } = useTranslation();
  const [state, setState] = useState({
    choose_tax_regime: "",
    tds_calculation: {
      income_from_sal: {
        actual_rent: 0,
        other_allowances: 0,
        salary_from_other: 0,
      },
      income_from_house: {
        income_from_hp: 0,
        municiple_tax_paid: 0,
        interest_self: 0,
        interest_let_out: 0,
      },
      income_from_os: {
        income_from_other: 0,
      },
      deductions: {
        Sec_80C_80CCC_80CCD: 0,
        Sec_80D: 0,
        Sec_80G: 0,
        Sec_80GG: 0,
        Sec_80TTA: 0,
        other: 0,
      },
    },
  });
  const [disabled, setDisabled] = useState();
  const [payrollStaffTdsProfileUpdate] = usePayrollStaffTdsProfileUpdate();
  const [payrollStaffTdsCompute] = usePayrollStaffTdsCompute();

  const { oneStaffProfile, oneStaffProfileRefetch } = useOneStaffProfile({
    sid: getQuery.state?.staffId,
    skip: !getQuery.state?.staffId,
  });
  useEffect(() => {
    if (getQuery.state?.staffId) oneStaffProfileRefetch();
  }, [getQuery.state?.staffId, oneStaffProfileRefetch]);

  useEffect(() => {
    if (oneStaffProfile?.staff?.tds_calculation) {
      let st = oneStaffProfile?.staff?.tds_calculation;
      setState({
        choose_tax_regime: oneStaffProfile?.staff?.choose_tax_regime,
        tds_calculation: {
          income_from_sal: {
            actual_rent: st?.income_from_sal?.actual_rent ?? 0,
            other_allowances: st?.income_from_sal?.other_allowances ?? 0,
            salary_from_other: st?.income_from_sal?.salary_from_other ?? 0,
          },
          income_from_house: {
            income_from_hp: st?.income_from_house?.income_from_hp ?? 0,
            municiple_tax_paid: st?.income_from_house?.municiple_tax_paid ?? 0,
            interest_self: st?.income_from_house?.interest_self ?? 0,
            interest_let_out: st?.income_from_house?.interest_let_out ?? 0,
          },
          income_from_os: {
            income_from_other: st?.income_from_os?.income_from_other ?? 0,
          },
          deductions: {
            Sec_80C_80CCC_80CCD: st?.deductions?.Sec_80C_80CCC_80CCD ?? 0,
            Sec_80D: st?.deductions?.Sec_80D ?? 0,
            Sec_80G: st?.deductions?.Sec_80G ?? 0,
            Sec_80GG: st?.deductions?.Sec_80GG ?? 0,
            Sec_80TTA: st?.deductions?.Sec_80TTA ?? 0,
            other: st?.deductions?.other ?? 0,
          },
        },
      });
    }
  }, [oneStaffProfile?.staff?.tds_calculation]);
  const onTile = (val) => {
    setState((prev) => ({
      ...prev,
      choose_tax_regime: val,
    }));
  };
  const onChange = (e, fd) => {
    setState((prev) => ({
      ...prev,
      tds_calculation: {
        ...prev.tds_calculation,
        [fd]: {
          ...prev.tds_calculation[fd],
          [e.target.name]: +e.target.value,
        },
      },
    }));
  };

  const onUpdate = () => {
    if (getQuery.state?.staffId) {
      setDisabled((pre) => !pre);
      payrollStaffTdsProfileUpdate({
        sid: getQuery.state?.staffId,
        bodyParameter: state,
      })
        .then(() => {
          setDisabled((pre) => !pre);
        })
        .catch({});
    }
  };

  const onTdsCalculate = () => {
    if (getQuery.state?.staffId) {
      setDisabled((pre) => !pre);
      payrollStaffTdsCompute({
        sid: getQuery.state?.staffId,
        // bodyParameter: state,
      })
        .then(() => {
          oneStaffProfileRefetch();
          setDisabled((pre) => !pre);
        })
        .catch({});
    }
  };

  // console.info("dsfdsgd", state, oneStaffProfile?.staff);
  return (
    <>
      <UserMemberContainerWrapper>
        <UserMemberContentWrapper>
          <div className={style.fm_header_container}>
            <h6>
              {getQuery.state?.name ?? ""} -{" "}
              {getQuery.state?.financialYear ?? ""}
            </h6>
          </div>
        </UserMemberContentWrapper>
        <BorderBottom />
        <UserMemberContentWrapper>
          <div className={stylePayroll.ped_container}>
            <TileWrapper
              label="tax_regime"
              customTileStyle={{
                margin: "0",
              }}
            >
              {regim_type?.map((tile) => (
                <TileCard
                  key={tile?.key}
                  tile={tile}
                  onTile={onTile}
                  activeTile={state?.choose_tax_regime}
                />
              ))}
            </TileWrapper>
            <BorderBottom />
            <h6 className={stylePayroll.pedc_heading}>
              {t("income_from_salary")}
            </h6>
            <FormInput
              label={t("actual_rent")}
              placeholder={t("please_enter_amount")}
              name="actual_rent"
              type="text"
              value={state.tds_calculation.income_from_sal.actual_rent}
              onChange={(e) => onChange(e, "income_from_sal")}
              customFormInputContainer={{
                width: "49%",
              }}
              // errorShow={errorField["form_checklist_lable"]}
            />
            <FormInput
              label={t("other_allowances")}
              placeholder={t("please_enter_amount")}
              name="other_allowances"
              type="text"
              value={state.tds_calculation.income_from_sal.other_allowances}
              onChange={(e) => onChange(e, "income_from_sal")}
              customFormInputContainer={{
                width: "49%",
              }}
              // errorShow={errorField["form_checklist_lable"]}
            />
            <FormInput
              label={t("salary_from_other")}
              placeholder={t("please_enter_amount")}
              name="salary_from_other"
              type="text"
              value={state.tds_calculation.income_from_sal.salary_from_other}
              onChange={(e) => onChange(e, "income_from_sal")}
              customFormInputContainer={{
                width: "49%",
              }}
              // errorShow={errorField["form_checklist_lable"]}
            />
            <BorderBottom />

            <h6 className={stylePayroll.pedc_heading}>
              {t("income_from_house")}
            </h6>
            <FormInput
              label={t("income_from_hp")}
              placeholder={t("please_enter_amount")}
              name="income_from_hp"
              type="text"
              value={state.tds_calculation.income_from_house.income_from_hp}
              onChange={(e) => onChange(e, "income_from_house")}
              customFormInputContainer={{
                width: "49%",
              }}
              // errorShow={errorField["form_checklist_lable"]}
            />
            <FormInput
              label={t("municiple_tax_paid")}
              placeholder={t("please_enter_amount")}
              name="municiple_tax_paid"
              type="text"
              value={state.tds_calculation.income_from_house.municiple_tax_paid}
              onChange={(e) => onChange(e, "income_from_house")}
              customFormInputContainer={{
                width: "49%",
              }}
              // errorShow={errorField["form_checklist_lable"]}
            />
            <FormInput
              label={t("interest_self")}
              placeholder={t("please_enter_amount")}
              name="interest_self"
              type="text"
              value={state.tds_calculation.income_from_house.interest_self}
              onChange={(e) => onChange(e, "income_from_house")}
              customFormInputContainer={{
                width: "49%",
              }}
              // errorShow={errorField["form_checklist_lable"]}
            />
            <FormInput
              label={t("interest_let_out")}
              placeholder={t("please_enter_amount")}
              name="interest_let_out"
              type="text"
              value={state.tds_calculation.income_from_house.interest_let_out}
              onChange={(e) => onChange(e, "income_from_house")}
              customFormInputContainer={{
                width: "49%",
              }}
              // errorShow={errorField["form_checklist_lable"]}
            />
            <BorderBottom />

            <h6 className={stylePayroll.pedc_heading}>
              {t("income_from_other_sources")}
            </h6>
            <FormInput
              label={t("income_from_other")}
              placeholder={t("please_enter_amount")}
              name="income_from_other"
              type="text"
              value={state.tds_calculation.income_from_os.income_from_other}
              onChange={(e) => onChange(e, "income_from_os")}
              customFormInputContainer={{
                width: "49%",
              }}
              // errorShow={errorField["form_checklist_lable"]}
            />
            <BorderBottom />

            <h6 className={stylePayroll.pedc_heading}>{t("deduction")}</h6>
            <FormInput
              label={t("Sec_80C_80CCC_80CCD")}
              placeholder={t("please_enter_amount")}
              name="Sec_80C_80CCC_80CCD"
              type="text"
              value={state.tds_calculation.deductions.Sec_80C_80CCC_80CCD}
              onChange={(e) => onChange(e, "deductions")}
              customFormInputContainer={{
                width: "49%",
              }}
              // errorShow={errorField["form_checklist_lable"]}
            />
            <FormInput
              label={t("Sec_80D")}
              placeholder={t("please_enter_amount")}
              name="Sec_80D"
              type="text"
              value={state.tds_calculation.deductions.Sec_80D}
              onChange={(e) => onChange(e, "deductions")}
              customFormInputContainer={{
                width: "49%",
              }}
              // errorShow={errorField["form_checklist_lable"]}
            />
            <FormInput
              label={t("Sec_80G")}
              placeholder={t("please_enter_amount")}
              name="Sec_80G"
              type="text"
              value={state.tds_calculation.deductions.Sec_80G}
              onChange={(e) => onChange(e, "deductions")}
              customFormInputContainer={{
                width: "49%",
              }}
              // errorShow={errorField["form_checklist_lable"]}
            />
            <FormInput
              label={t("Sec_80GG")}
              placeholder={t("please_enter_amount")}
              name="Sec_80GG"
              type="text"
              value={state.tds_calculation.deductions.Sec_80GG}
              onChange={(e) => onChange(e, "deductions")}
              customFormInputContainer={{
                width: "49%",
              }}
              // errorShow={errorField["form_checklist_lable"]}
            />
            <FormInput
              label={t("Sec_80TTA")}
              placeholder={t("please_enter_amount")}
              name="Sec_80TTA"
              type="text"
              value={state.tds_calculation.deductions.Sec_80TTA}
              onChange={(e) => onChange(e, "deductions")}
              customFormInputContainer={{
                width: "49%",
              }}
              // errorShow={errorField["form_checklist_lable"]}
            />
            <FormInput
              label={t("other")}
              placeholder={t("please_enter_amount")}
              name="other"
              type="text"
              value={state.tds_calculation.deductions.other}
              onChange={(e) => onChange(e, "deductions")}
              customFormInputContainer={{
                width: "49%",
              }}
              // errorShow={errorField["form_checklist_lable"]}
            />
            <CreateButton
              label="update"
              onAction={onUpdate}
              customStyle={{
                width: "100%",
              }}
            />
            <CreateButton
              label="tds_calculate"
              onAction={onTdsCalculate}
              customStyle={{
                width: "100%",
              }}
            />
          </div>
          <div className={payrollStyle.as_flex}>
            <p>{t("tds_rate")}: </p>
            <h6>
              {oneStaffProfile?.staff?.tds_calculation?.tds_calculate?.rate
                ? `${oneStaffProfile?.staff?.tds_calculation?.tds_calculate?.rate} %`
                : "N/A"}
            </h6>
          </div>
          <div className={payrollStyle.as_flex}>
            <p>{t("tds_monthly")}: </p>
            <h6>
              {oneStaffProfile?.staff?.tds_calculation?.tds_calculate?.month}
            </h6>
          </div>
          <div className={payrollStyle.as_flex}>
            <p>{t("tds_annualy")}: </p>
            <h6>
              {oneStaffProfile?.staff?.tds_calculation?.tds_calculate?.annual}
            </h6>
          </div>
        </UserMemberContentWrapper>
      </UserMemberContainerWrapper>
      {disabled && <QvipleLoader />}
    </>
  );
};

export default PayrollTdsCalculationUpdateForm;
export const regim_type = [
  {
    key: "OLD_TAX",
    name: "old_tax_structure",
  },
  {
    key: "NEW_TAX",
    name: "new_tax_structure",
  },
];
