import { useTranslation } from "react-i18next";
import style from "../../StoreManager/StoreManager.module.css";
import { imageShowUrl, pdfCdnUrl } from "../../../Services/UrlConfig/BaseUrl";
import {
  assestsFeedUrl,
  assestsUserFinanceUrl,
} from "../../../Services/UrlConfig/AssestsBaseUrl";
const PayrollFundAllocationSatffListCard = ({ staff }) => {
  const { t } = useTranslation();

  const onSalarySlip = () => {
    if (staff?.staff_obj?.slip) {
      const newWindow = window.open(
        `${pdfCdnUrl}/${staff?.staff_obj?.slip_key}`,
        "_blank",
        "noopener,noreferrer"
      );
      if (newWindow) newWindow.opener = null;
    }
  };
  return (
    <>
      <div className={style.smc_card}>
        <div
          className={style.smc_card_contatiner}
          style={{
            justifyContent: "space-between",
            width: "100%",
          }}
          // onClick={onToggle}
        >
          <div className={style.smc_card_contatiner}>
            <img
              src={
                staff?.staffProfilePhoto
                  ? `${imageShowUrl}/${staff?.staffProfilePhoto}`
                  : `${assestsFeedUrl}/feed-user-avatar.svg`
              }
              loading="lazy"
              alt="avatar"
              style={{
                height: "2.3rem",
                width: "2.3rem",
                borderRadius: "50%",
              }}
            />

            <div className={style.smc_card_contatiner_inner}>
              <h6>{`${staff?.staffFirstName ?? ""} ${
                staff?.staffMiddleName ?? ""
              } ${staff?.staffLastName ?? ""}`}</h6>
              <p>
                {t("index_number_1")} {staff?.staffROLLNO ?? ""}
              </p>
              <p>
                {t("net_pay")}: {staff?.staff_obj?.value ?? ""}
              </p>
            </div>
          </div>
          <img
            src={`${assestsUserFinanceUrl}/receipt.svg`}
            alt="receipt icon"
            title="Download Salary Slip"
            style={{
              width: "auto",
              height: "2rem",
            }}
            onClick={onSalarySlip}
          />
        </div>
      </div>
    </>
  );
};

export default PayrollFundAllocationSatffListCard;
