import { useLocation, useSearchParams } from "react-router-dom";
import UserMemberMainWrapper from "../../Layout/UserMemberTab/UserMemberMainWrapper";
import QvipleLoader from "../../Loader/QvipleLoader";
import { Suspense } from "react";
import UserMemberTabsWrapper from "../../Ui/UserMember/UserMemberTabsWrapper";
import Loading from "../../Loader/Loading";
import PayrollManagerProfile from "./PayrollManagerProfile";
import { usePayrollDashboardMaster } from "../../Hooks/ApiHooks/PayrollManager/payroll-manager-api";
import PayrollStaff from "./PayrollStaff/PayrollStaff";
import PayrollSalaryComputationConfig from "./PayrollSalaryComputation/PayrollSalaryComputationConfig";
import SalaryDays from "./PayrollSalaryComputation/SalaryDays/SalaryDays";
import AttendanceSheet from "./PayrollSalaryComputation/AttendanceSheet/AttendanceSheet";
import PayrollConfiguration from "./PayrollConfiguration/PayrollConfiguration";
import PayrollDefaultSalaryStructure from "./PayrollDefaultSalaryStructure/PayrollDefaultSalaryStructure";
import PayrollOneHead from "./PayrollConfiguration/PayrollOneHead/PayrollOneHead";
import PayrollFundAllocationSatffList from "./PayrollFundAllocation/PayrollFundAllocationSatffList";
import PayrollTdsCalculation from "./PayrollTdsCalculation/PayrollTdsCalculation";
import PayrollForm16 from "./PayrollForm16/PayrollForm16";

const PayrollManager = () => {
  const getQuery = useLocation();
  const [searchParams] = useSearchParams();
  const { payrollDashboardMaster, payrollDashboardMasterLoading } =
    usePayrollDashboardMaster({
      data: {
        pid: getQuery.state?.pid,
        mid: "",
      },
      skip: !getQuery.state?.pid,
    });

  //   console.info("payrollDashboardMaster", payrollDashboardMaster);
  return (
    <>
      <UserMemberMainWrapper>
        <Suspense fallback={<QvipleLoader />}>
          {!searchParams.get("type") ||
          ["employer-detail", "allocation-fund"]?.includes(
            searchParams.get("type")
          ) ? (
            <UserMemberTabsWrapper>
              <PayrollManagerProfile
                tabManage={payrollDashboardMaster?.tab_manage}
                pid={getQuery.state?.pid}
                instituteId={payrollDashboardMaster?.institute?._id}
              />
              {payrollDashboardMasterLoading && <Loading />}
            </UserMemberTabsWrapper>
          ) : searchParams.get("type") === "staff" ? (
            <PayrollStaff
              instituteId={payrollDashboardMaster?.institute?._id}
            />
          ) : searchParams.get("type") === "salary-computation" ? (
            <PayrollSalaryComputationConfig
              instituteId={payrollDashboardMaster?.institute?._id}
            />
          ) : searchParams.get("type") === "salary-days" ? (
            <SalaryDays instituteId={payrollDashboardMaster?.institute?._id} />
          ) : searchParams.get("type") === "attendance-sheet" ? (
            <AttendanceSheet
              instituteId={payrollDashboardMaster?.institute?._id}
            />
          ) : searchParams.get("type") === "config" ? (
            <PayrollConfiguration
              instituteId={payrollDashboardMaster?.institute?._id}
              pid={getQuery.state?.pid}
            />
          ) : searchParams.get("type") === "default-structure" ? (
            <PayrollDefaultSalaryStructure
              instituteId={payrollDashboardMaster?.institute?._id}
              pid={getQuery.state?.pid}
            />
          ) : searchParams.get("type") === "one-head" ? (
            <PayrollOneHead
              instituteId={payrollDashboardMaster?.institute?._id}
              pid={getQuery.state?.pid}
            />
          ) : searchParams.get("type") === "allocation-staff" ? (
            <PayrollFundAllocationSatffList
              instituteId={payrollDashboardMaster?.institute?._id}
              pid={getQuery.state?.pid}
            />
          ) : searchParams.get("type") === "tds-calculation" ? (
            <PayrollTdsCalculation
              instituteId={payrollDashboardMaster?.institute?._id}
              pid={getQuery.state?.pid}
            />
          ) : searchParams.get("type") === "form-16" ? (
            <PayrollForm16
              instituteId={payrollDashboardMaster?.institute?._id}
              pid={getQuery.state?.pid}
            />
          ) : null}
        </Suspense>
      </UserMemberMainWrapper>
    </>
  );
};

export default PayrollManager;
