import { useTranslation } from "react-i18next";
import { useLocation, useSearchParams } from "react-router-dom";
import style from "../../FinanceManager/FinanceManager.module.css";
import UserMemberContainerWrapper from "../../../Ui/UserMember/UserMemberContainerWrapper";
import UserMemberContentWrapper from "../../../Ui/UserMember/UserMemberContentWrapper";
import BorderBottom from "../../../Ui/Border/BorderBottom";
import { useCalendar } from "../../../Hooks/CustomHooks/use-calendar";
import dayjs from "dayjs";
import { useCallback, useEffect, useState } from "react";
import SelectFinancialYear from "../SelectFinancialYear";
import { useInstituteAllStaff } from "../../../Hooks/ApiHooks/FinanceManager/finance-manager-api";
import { nextPage } from "../../../Utils/Functions/nextPage";
import { customDebounce } from "../../../Utils/Functions/debounce";
import QvipleLoader from "../../../Loader/QvipleLoader";
import Loading from "../../../Loader/Loading";
import EmptyMessage from "../../../Utils/EmptyMessage/EmptyMessage";
import PayrollTdsCalculationUpdateForm from "./PayrollTdsCalculationUpdateForm";
import { useInView } from "react-intersection-observer";
import { assestsNavbarUrl } from "../../../Services/UrlConfig/AssestsBaseUrl";
import PayrollTdsCalculationCard from "./PayrollTdsCalculationCard";
const PayrollTdsCalculation = ({ instituteId, pid }) => {
  const { t } = useTranslation();
  const getQuery = useLocation();
  const [searchParams] = useSearchParams();
  const { selectedDate, setSelectedDate } = useCalendar();
  const [financialYear, setFinancialYear] = useState(false);

  const [search, setSearch] = useState("");
  const [page, setPage] = useState(1);
  const [state, setState] = useState(true);
  const [ref, inView] = useInView({ skip: search });

  const { instituteAllStaff, instituteAllStaffLoading } = useInstituteAllStaff({
    data: {
      id: instituteId,
      page: page,
      limit: 10,
      search: search,
    },
    skip: !instituteId,
  });
  useEffect(() => {
    if (inView && state) setPage((page) => page + 1);
  }, [inView, state]);
  useEffect(() => {
    if (instituteAllStaff?.list?.length) {
      let obj = nextPage(instituteAllStaff?.list?.length);
      setState(() => obj);
    }
  }, [instituteAllStaff?.list]);

  const onDebounce = useCallback(
    customDebounce((val) => {
      setPage(() => 1);
      setSearch(val);
    }, 1500),
    []
  );

  const onSearchEvent = (e) => {
    if (e.target.value) onDebounce(e.target.value);
    else {
      setPage(() => 1);
      setSearch("");
    }
  };

  const onYearToggle = () => {
    setFinancialYear((pre) => !pre);
  };
  const onYearChoose = (value) => {
    setSelectedDate(
      (prev) => new Date(`${prev.getMonth() + 1} ${prev.getDate()} ${value}`)
    );
    onYearToggle();
  };
  return (
    <>
      {searchParams.get("n") === "staff" ? (
        <PayrollTdsCalculationUpdateForm pid={pid} />
      ) : (
        <UserMemberContainerWrapper>
          <UserMemberContentWrapper>
            <div className={style.fm_header_container}>
              <h6>{t("tds_calculation")}</h6>
              <div className={style.fm_search}>
                <section className={style.fm_search_container}>
                  <div className={style.fm_search_container_inner}>
                    <img
                      className={style.fm_search_icon}
                      alt="search icon"
                      src={`${assestsNavbarUrl}/navbar-search.svg`}
                    />
                    <input
                      type="text"
                      placeholder={t("search")}
                      onChange={onSearchEvent}
                    />
                  </div>
                </section>
                <div className={style.fm_header_start_container}>
                  <h6
                    style={{
                      cursor: "pointer",
                    }}
                    onClick={onYearToggle}
                  >
                    {t("financial_year")} : {dayjs(selectedDate).format("YYYY")}
                  </h6>
                </div>
              </div>
            </div>
          </UserMemberContentWrapper>
          <BorderBottom />
          <UserMemberContentWrapper>
            {instituteAllStaff?.list?.map((staff, index) =>
              instituteAllStaff?.list?.length === index + 1 ? (
                <div key={staff?._id} ref={ref}>
                  <PayrollTdsCalculationCard
                    staff={staff}
                    financialYear={dayjs(selectedDate).format("YYYY")}
                  />
                </div>
              ) : (
                <PayrollTdsCalculationCard
                  staff={staff}
                  key={staff?._id}
                  financialYear={dayjs(selectedDate).format("YYYY")}
                />
              )
            )}
            {instituteAllStaffLoading ? (
              search ? (
                <QvipleLoader />
              ) : (
                <Loading />
              )
            ) : search ? (
              instituteAllStaff?.list?.length === 0 && (
                <EmptyMessage title="No staffs found related this search." />
              )
            ) : (
              instituteAllStaff?.list?.length === 0 && (
                <EmptyMessage title="No staffs." />
              )
            )}
          </UserMemberContentWrapper>
        </UserMemberContainerWrapper>
      )}

      {financialYear && (
        <SelectFinancialYear
          onClose={onYearToggle}
          onYearChoose={onYearChoose}
        />
      )}
    </>
  );
};

export default PayrollTdsCalculation;
