import { useMemo, useState } from "react";
import Modal from "../../Ui/Modal/Modal";
import style from "../../Utils/Calendar/Calendar.module.css";
import dayjs from "dayjs";
import { assestsUtilityUrl } from "../../Services/UrlConfig/AssestsBaseUrl";
import { getYearList, useCalendar } from "../../Hooks/CustomHooks/use-calendar";

const SelectFinancialYear = ({ onClose, onYearChoose }) => {
  const { selectedDate } = useCalendar();

  const allYear = getYearList();
  const currentYear = useMemo(
    () => allYear.indexOf(selectedDate.getFullYear()),
    [selectedDate, allYear]
  );
  const [yearCount, setYearCount] = useState(0);
  const showYear = useMemo(
    () =>
      allYear.slice(
        currentYear + 12 * yearCount,
        yearCount ? currentYear + 12 * yearCount + 12 : currentYear + 12
      ),
    [allYear, yearCount, currentYear]
  );

  const onChooseYear = (value) => {
    onYearChoose(value);
  };

  const onPrev = () => {
    setYearCount((prev) => (prev = prev - 1));
  };
  const onNext = () => {
    setYearCount((prev) => (prev = prev + 1));
  };

  return (
    <Modal
      onClose={onClose}
      customBackdropStyle={{
        zIndex: "2000",
      }}
      customOverlayStyle={{
        zIndex: "2000",
      }}
    >
      <div className={style.calendar}>
        <div className={style.calendar_top}>
          <img
            src={`${assestsUtilityUrl}/next.svg`}
            alt="previous icon"
            onClick={onPrev}
            style={{
              rotate: "180deg",
            }}
          />
          <h6>
            <span>{dayjs(selectedDate).format("YYYY")}</span>
          </h6>
          <img
            src={`${assestsUtilityUrl}/next.svg`}
            alt="next icon"
            onClick={onNext}
          />
        </div>
        <div className={style.calendar_month}>
          {showYear?.map((name) => (
            <p
              className={
                dayjs(selectedDate).format("YYYY") === `${name}`
                  ? `${style.calendar_month_value} ${style.calendar_month_value_active}`
                  : style.calendar_month_value
              }
              key={name}
              onClick={() => onChooseYear(name)}
            >
              {name}
            </p>
          ))}
        </div>
      </div>
    </Modal>
  );
};

export default SelectFinancialYear;
