import { useTranslation } from "react-i18next";
import style from "../../UiTab.module.css";
import { Link } from "react-router-dom";
const SquareTabCard = ({
  label,
  iconUrl,
  url,
  state,
  isAuthorized,
  isVisible,
  authorizedKey,
  customStyleContainer,
}) => {
  const { t } = useTranslation();

  return url ? (
    <>
      {!isAuthorized
        ? isVisible &&
          isVisible[authorizedKey] && (
            <div
              className={style.square_tab_card}
              style={{
                opacity: "0.5",
                ...customStyleContainer,
              }}
            >
              <img src={iconUrl} alt="card icon" />
              <p>{t(label)}</p>
            </div>
          )
        : isVisible &&
          isVisible[authorizedKey] && (
            <Link
              className={style.square_tab_card}
              to={url}
              state={state}
              style={customStyleContainer}
            >
              <img src={iconUrl} alt="card icon" />
              <p>{t(label)}</p>
            </Link>
          )}
    </>
  ) : (
    <div className={style.square_tab_card} style={customStyleContainer}>
      <img src={iconUrl} alt="card icon" />
      <p>{t(label)}</p>
    </div>
  );
};

export default SquareTabCard;
