import { Link, useLocation, useParams } from "react-router-dom";
import { assestsUserFinanceUrl } from "../../../Services/UrlConfig/AssestsBaseUrl";
import { useTranslation } from "react-i18next";
import style from "../../StoreManager/StoreManager.module.css";
import QvipleLoader from "../../../Loader/QvipleLoader";
import { useState } from "react";
import { pdfCdnUrl } from "../../../Services/UrlConfig/BaseUrl";
import { usePayrollAllocationFundExportExcel } from "../../../Hooks/ApiHooks/PayrollManager/payroll-manager-api";

const PayrollFundAllocationMonthCard = ({
  mt,
  amountObj,
  financialYear,
  pid,
}) => {
  const { t } = useTranslation();
  const params = useParams();
  const getQuery = useLocation();
  const [disabled, setDisabled] = useState(false);

  const [payrollAllocationFundExportExcel] =
    usePayrollAllocationFundExportExcel();
  const onExportExcel = (e) => {
    e.preventDefault();
    setDisabled((pre) => !pre);
    payrollAllocationFundExportExcel({
      pid: pid,
      bodyParameter: {
        month: mt?.value,
        year: financialYear,
      },
    })
      .then((res) => {
        if (res?.data?.data) {
          setDisabled((pre) => !pre);
          const newWindow = window.open(
            // `${pdfCdnUrl}/${res?.data?.data}`,
            `https://qviple-dev.s3.ap-south-1.amazonaws.com/${res?.data?.data}`,
            "_blank",
            "noopener,noreferrer"
          );
          if (newWindow) newWindow.opener = null;
        } else {
          setDisabled((pre) => !pre);
        }
      })
      .catch({});
  };
  return (
    <>
      <Link
        to={`/q/${params.username}/member/staff/payroll?type=allocation-staff`}
        state={{
          ...getQuery.state,
          headCard: mt,
          financialYear: financialYear,
        }}
      >
        <div className={style.smc_card}>
          <div className={style.smc_card_contatiner}>
            <img
              src={`${assestsUserFinanceUrl}/category.svg`}
              alt="categor icon"
            />
            <div className={style.smc_card_contatiner_inner}>
              <h6>{mt?.name ?? ""}</h6>
              <p>
                {t("amount")} {": "} {amountObj && amountObj[mt?.value]}
              </p>
            </div>
          </div>
          <img
            src={`${assestsUserFinanceUrl}/receipt.svg`}
            alt="receipt icon"
            title="Download Fees Receipt"
            style={{
              width: "auto",
              height: "2rem",
            }}
            onClick={onExportExcel}
          />
        </div>
      </Link>
      {disabled && <QvipleLoader />}
    </>
  );
};

export default PayrollFundAllocationMonthCard;
