import { useTranslation } from "react-i18next";
import { useLocation, useSearchParams } from "react-router-dom";
import { useCalendar } from "../../../Hooks/CustomHooks/use-calendar";
import { useState } from "react";
import style from "../../FinanceManager/FinanceManager.module.css";
import dayjs from "dayjs";
import SelectFinancialYear from "../SelectFinancialYear";
import PayrollFundAllocationMonthCard from "./PayrollFundAllocationMonthCard";
import { usePayrollAlocationMonthList } from "../../../Hooks/ApiHooks/PayrollManager/payroll-manager-api";
const PayrollFundAllocation = ({ pid, instituteId }) => {
  const { t } = useTranslation();
  const getQuery = useLocation();
  const [searchParams] = useSearchParams();
  const { selectedDate, setSelectedDate } = useCalendar();
  const [financialYear, setFinancialYear] = useState(false);

  const { payrollAlocationMonthList } = usePayrollAlocationMonthList({
    data: {
      pid: pid,
    },
    skip: !pid,
  });
  const onYearToggle = () => {
    setFinancialYear((pre) => !pre);
  };

  const onYearChoose = (value) => {
    setSelectedDate(
      (prev) => new Date(`${prev.getMonth() + 1} ${prev.getDate()} ${value}`)
    );
    onYearToggle();
  };

  // console.info("payrollAlocationMonthList", payrollAlocationMonthList);
  return (
    <>
      <div
        className={style.fm_header_container}
        style={{
          marginTop: "1rem",
        }}
      >
        <div className={style.fm_header_start_container}></div>
        <div className={style.fm_header_start_container}>
          <h6
            style={{
              cursor: "pointer",
            }}
            onClick={onYearToggle}
          >
            {t("financial_year")} : {dayjs(selectedDate).format("YYYY")}
          </h6>
        </div>
      </div>
      {monthList?.map((mt, index) => (
        <PayrollFundAllocationMonthCard
          key={mt?.name}
          mt={mt}
          index={index}
          amountObj={payrollAlocationMonthList}
          financialYear={dayjs(selectedDate).format("YYYY")}
          pid={pid}
        />
      ))}
      {financialYear && (
        <SelectFinancialYear
          onClose={onYearToggle}
          onYearChoose={onYearChoose}
        />
      )}
    </>
  );
};

export default PayrollFundAllocation;

