import { useTranslation } from "react-i18next";
import style from "../../../FinanceManager/FinanceManager.module.css";
import { useLocation, useNavigate, useParams } from "react-router-dom";
import BorderBottom from "../../../../Ui/Border/BorderBottom";
import UserMemberContainerWrapper from "../../../../Ui/UserMember/UserMemberContainerWrapper";
import UserMemberContentWrapper from "../../../../Ui/UserMember/UserMemberContentWrapper";
import payrollStyle from "../../PayrollManager.module.css";
import CreateButton from "../../../../Ui/Button/CreateButton";
import { usePayrollOneStaffSalaryDays } from "../../../../Hooks/ApiHooks/PayrollManager/payroll-manager-api";

const SalaryDays = () => {
  const { t } = useTranslation();
  const params = useParams();
  const getQuery = useLocation();
  const navigate = useNavigate();

  const { payrollOneStaffSalaryDays } = usePayrollOneStaffSalaryDays({
    data: {
      sid: getQuery.state?.staffId,
      month: getQuery.state?.headCard?.value,
      year: getQuery.state?.financialYear,
    },
    skip: !getQuery.state?.staffId,
  });
  const onNavigate = (val) => {
    navigate(`/q/${params.username}/member/staff/payroll?type=${val}`, {
      state: getQuery.state,
    });
  };

  // console.info("Sdfdgfd", getQuery.state, payrollOneStaffSalaryDays);
  return (
    <>
      <UserMemberContainerWrapper>
        <UserMemberContentWrapper>
          <div className={style.fm_header_container}>
            <div className={style.fm_header_start_container}>
              <h6>
                {getQuery.state?.name ?? ""} -
                {getQuery.state?.headCard?.name ?? ""},{" "}
                {getQuery.state?.financialYear ?? ""} {` `} {t("salary_days")}
              </h6>
            </div>
          </div>
        </UserMemberContentWrapper>

        <BorderBottom />
        <UserMemberContentWrapper
          customStyle={{
            paddingTop: "0",
          }}
        >
          <div className={payrollStyle.ast_header}>
            <h6
              style={{
                // flex: "0.4 0.4",
                width: "50%",
                // flex: "0",
              }}
            >
              {t("name")}
            </h6>
            <h6
              style={{
                borderRightWidth: "1px",
                width: "50%",
              }}
            >
              {t("count")}
            </h6>
          </div>
          <section className={payrollStyle.ast_body}>
            <div
              className={payrollStyle.astb_day}
              style={{
                width: "50%",
              }}
            >
              <h6>{t("total_wotking_days")}</h6>
              <h6>{t("unpaid_leaves")}</h6>
              <h6>{t("salary_days")}</h6>
              <h6>{t("paid_leaves")}</h6>
              <h6>{t("holidays")}</h6>
              <h6>{t("present")}</h6>
            </div>
            <div
              className={payrollStyle.astb_val}
              style={{
                width: "50%",
              }}
            >
              <h6
                style={{
                  borderRightWidth: "1px",
                }}
              >
                {payrollOneStaffSalaryDays?.total_working_days ?? 0}
              </h6>
              <h6
                style={{
                  borderRightWidth: "1px",
                }}
              >
                {payrollOneStaffSalaryDays?.unpaid_leaves ?? 0}
              </h6>
              <h6
                style={{
                  borderRightWidth: "1px",
                }}
              >
                {payrollOneStaffSalaryDays?.sal_day ?? 0}
              </h6>
              <h6
                style={{
                  borderRightWidth: "1px",
                }}
              >
                {payrollOneStaffSalaryDays?.paid_leaves ?? 0}
              </h6>
              <h6
                style={{
                  borderRightWidth: "1px",
                }}
              >
                {payrollOneStaffSalaryDays?.holiday ?? 0}
              </h6>
              <h6
                style={{
                  borderRightWidth: "1px",
                }}
              >
                {payrollOneStaffSalaryDays?.present ?? 0}
              </h6>
            </div>
          </section>
          <div className={payrollStyle.sd_tab}>
            <CreateButton
              label="attendance_sheet"
              onAction={() => onNavigate("salary-computation")}
            />
            <CreateButton
              label="salary_computation"
              onAction={() => onNavigate("salary-computation&n=computation")}
            />
            <CreateButton
              label="salary_slip"
              onAction={() => onNavigate("salary-computation&n=slip")}
            />
          </div>
        </UserMemberContentWrapper>
      </UserMemberContainerWrapper>
    </>
  );
};

export default SalaryDays;
